.picker-container .picker-disabled.active:after {content:"";position:absolute;top:0;left:0;width:100%;height:100%;background-image:radial-gradient(circle, #000000 10%, transparent 10.01%);background-repeat:no-repeat;background-position:50%;pointer-events:none;background-size:0% 0%;opacity:0;-webkit-transition:background .5s, opacity 1s;transition: background .5s, opacity 1s;}
